.login-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 66px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 50px;
  h2 {
      margin-bottom: 30px;
  }

  form {
      display: flex;
      flex-direction: column;

      label {
          margin-bottom: 10px;
          display: flex;
          flex-direction: column;

          input {
              padding: 10px;
              margin-top: 5px;
              border: 1px solid #ccc;
              border-radius: 5px;
              box-sizing: border-box;
          }

          .password-label {
            position: relative;
            display: flex;
            align-items: center;
            
    
            input {
                padding-right: 2.5em;
            }
    
            .toggle-password {
                position: absolute;
                right: 10px;
                background: none;
                border: none;
                cursor: pointer;
        
                  &:hover {
                      color: #333;
                  }

                  &:focus {
                      outline: none;
                  }
              }
          }
      }

      button[type="submit"] {
          padding: 15px;
          background-color: #333;
          color: white;
          border: none;
          border-radius: 10px;
          cursor: pointer;

          &:hover {
              background-color: #575757;
          }
      }

      p {
          margin-top: 20px;
          text-align: center;

      }
      a{
            text-decoration: none;
            font-size: 15px;
            font-weight: 500;
            color: rgb(139, 0, 65);
            text-transform: uppercase;
    }
  }
}
